<template>
	<div>
		<HeaderComponent :view-title="templateTitle"></HeaderComponent>
		<div id="activityTem-main">
			<ul class="plate">
				<li class="plate-img"><img src="../../assets/images/default.jpg"></li>
				<li class="plate-text clear">
					<p class="left">大转盘</p>
					<p class="right"><span @click="showDialog()">查看示例</span></p>
				</li>
			</ul>
			<ul class="plate">
				<li class="plate-img"><img src="../../assets/images/default.jpg"></li>
				<li class="plate-text clear">
					<p class="left">大转盘</p>
					<p class="right"><span @click="showDialog()">查看示例</span></p>
				</li>
			</ul>
			<ul class="plate">
				<li class="plate-img"><img src="../../assets/images/default.jpg"></li>
				<li class="plate-text clear">
					<p class="left">大转盘</p>
					<p class="right"><span @click="showDialog()">查看示例</span></p>
				</li>
			</ul>
		</div>
		<!-- 弹出模态框 -->
		<div class="dialog-modal" v-show="isShowDialog" @touchmove.prevent>
			<div class="dialog-wrapper">
				<!-- <transition name="dialog"> -->
					<div class="dialog-close-btn">
						<svg class="iconActivityTem" aria-hidden="true" @click="closeDialog()">
	  						<use xlink:href="#icon-wrong"></use>
						</svg>
						<p class="closeLine"></p>
					</div>
					<ul class="dialog-container">
						<li class="container-title">大转盘</li>
						<li class="QRcode"><img src="../../assets/images/huodongle.png"></li>
						<li>微信扫一扫查看活动页面</li>
					</ul>
				<!-- </transition> -->
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		templateTitle:Object,
		data(){
			return{
				templateTitle:{
					name:'活动模板',
					showCode:1
				},
				isShowDialog:false
			}
		},
		mounted(){},
		methods:{
			showDialog:function(){
				this.isShowDialog = !this.isShowDialog;
			},
			closeDialog:function(){
				this.isShowDialog = false;
			}
		},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	.iconActivityTem {
	   width: 1.3rem; height: 1.3rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#B5B7B9;
	}
	#activityTem-main{
		width:94%;
		margin:0 auto;
		margin-top:2.6rem;
		.plate{
			margin-bottom: 0.4rem;
			.plate-img{
				img{
					width:100%;
					height:7.2rem;
					vertical-align: bottom;
					border-radius:0.4rem 0.4rem 0 0;
				}
			}
			.plate-text{
				background:#fff;
				border-radius:0 0 0.4rem 0.4rem;
				p{
					width:50%;
					height:1.8rem;
					line-height:1.8rem;
					@include sc(0.7rem,#4C4C4C);
				}
				p.left{
					padding-left:0.3rem;
				}
				p.right{
					padding-right:0.3rem;
					text-align: right;
					span{
						text-decoration: underline;
						color:#F67979;
					}
				} 
			}
		}
	}
	/* 模态框样式 */
	.dialog-modal{
		width:100%;
		height:100%;
		position:fixed;
		/* top:2.2rem; */
		top:0;
		left:0;
		background:rgba(102,102,102,0.8);
		z-index:99;
		.dialog-wrapper{
			width:13rem;
			height:13rem;
			background:#fff;
			@include center;
			@include borderRadius(0.3rem);
			.dialog-container{
				width:8rem;
				@include center;
				@include sc(0.7rem,#000);
				text-align:center;
				li:not(:last-child){
					margin-bottom:1rem;
				}
				li.container-title{
					@include sc(0.85rem,#000);
				}
				li.QRcode img{
					display: block;
					vertical-align: bottom;
					width:8rem;
					height:7rem;
				}
			}
			.dialog-close-btn{
				position: absolute;
    			top: -3rem;
    			right: 0;
				.closeLine{
					height:1.7rem;
					border-left:1px solid #B5B7B9;
					@include cl;
					top:1.3rem;
				}
			}
		}
	}
</style>


